.grid-container {
  width: 100%;
  max-width: 100%;
  background: green;
  overflow: hidden;
  color: white;
  text-align: center;
}

.grid-container .row {
  overflow: hidden;
}

.grid-container .row:before, .grid-container .row:after {
  content: "";
  display: table;
}

.grid-container .row:after {
  clear: both;
}

.grid-container .row [class*='col-'] {
  min-height: 1px;
  float: left;
  margin-bottom: -99999px;
  padding-bottom: 99999px;
  border-top: 2px solid white;
}

.grid-container .row .col-12 {
  width: 100%;
}

.grid-container .row .col-11 {
  width: 91.66667%;
}

.grid-container .row .col-10 {
  width: 83.33333%;
}

.grid-container .row .col-9 {
  width: 75%;
}

.grid-container .row .col-8 {
  width: 66.66667%;
}

.grid-container .row .col-7 {
  width: 58.33333%;
}

.grid-container .row .col-6 {
  width: 50%;
}

.grid-container .row .col-5 {
  width: 41.66667%;
}

.grid-container .row .col-4 {
  width: 33.33333%;
}

.grid-container .row .col-3 {
  width: 25%;
}

.grid-container .row .col-2 {
  width: 16.66667%;
}

.grid-container .row .col-1 {
  width: 8.33333%;
}

@media (max-width: 640px) {
  .grid-container .row[class*='col-'] {
    float: none;
    width: 100%;
  }
}

.grid-container .blue {
  background-color: deepskyblue;
}

.grid-container .violet {
  background-color: blueviolet;
}

.grid-container .darkblue {
  background-color: dodgerblue;
}

.grid-container .yelow {
  background-color: lightyellow;
}

.mar {
  border-bottom: 2px solid white;
}

.content {
  color: black;
  padding-top: 20px;
}
